@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@500;700&display=swap);
.list {
    list-style: none;
    counter-reset: steps;
    font-family: 'Baloo Tamma 2', cursive;
    font-weight: 500;
	font-size: 1.2rem;
}

.list li {
    counter-increment: steps;
	margin-top: 1rem;
	
}

.add-button {
    position: fixed;
    right: 15px;
    bottom: 20px;
    color: #218c74;
    font-size: 60px;
}


.button-submit {
	display: inline-flex;
  	align-items: center;
  	justify-content: center;
	background-color: #218c74;
	color: #fff;
	border-radius: 8px;
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
	padding: 0.25em 0.75em;
	min-width: 10ch;
	min-height: 44px;
	margin-top: 1.1rem;
	margin-left: 22%;
	text-align: center;
	line-height: 1.1;
	font: inherit;
	border: none;
	cursor: pointer;
	transition: 220ms all ease-in-out;
	outline: none;
}

.button-submit:hover,
.button-submit:active {
	background-color: #1a705d;
	transition: 220ms all ease-in-out;
}

.form__group {
	position: relative;
	padding: 15px 0 0;
	margin-top: 10px;
	width: 50%;
}

.form__field {
    font-family: 'Baloo Tamma 2', cursive;
	width: 100%;
	border: 0;
	border-bottom: 2px solid #9b9b9b;
	outline: 0;
	font-size: 1.3rem;
	color: black;
	padding: 7px 0;
	background: transparent;
	transition: border-color 0.2s;
}

.form__field::-webkit-input-placeholder {
	color: transparent;
}

.form__field:-ms-input-placeholder {
	color: transparent;
}

.form__field::-ms-input-placeholder {
	color: transparent;
}

.form__field::placeholder {
	color: transparent;
}

.form__field:-ms-input-placeholder ~ .form__label {
	font-size: 1.3rem;
	cursor: text;
	top: 20px;
}

.form__field:placeholder-shown ~ .form__label {
	font-size: 1.3rem;
	cursor: text;
	top: 20px;
}

.form__label {
	position: absolute;
	top: 0;
	display: block;
	transition: 0.2s;
	font-size: 1rem;
	color: #9b9b9b;
}

.form__field:focus {
	padding-bottom: 6px;
	font-weight: 500;
	border-width: 3px;
	border-image: linear-gradient(to right, #218c74, #38ef7d);
	border-image-slice: 1;
}

.form__field:focus ~ .form__label {
	position: absolute;
	top: 10;
	display: block;
	transition: 0.2s;
	font-size: 1rem;
	color: #218c74;
	font-weight: 700;
}

/* reset input */
.form__field:required, .form__field:invalid {
	box-shadow: none;
}

