li {
    counter-increment: steps;
    margin-top: 1rem;
} 

.item::before {
    content: counter(steps);
    margin-right: 0.5rem;
    background: #ff6f00;
    color: white;
    width: 1.2em;
    height: 1.2em;
    border-radius: 50%;
    display: inline-grid;
    place-items: center;
    line-height: 1.4em;
}

.done::before {
    background: #218c74;
}

.done {
    text-decoration: line-through;
}

.pending::before {
    background: #ff6f00;
}

.button-delete {
	float: right;
	
} 
